import Vue from 'vue'
import Vuex from 'vuex'

import {
  model,
} from "./mock.js";


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentModel: model,
    currentModelAdd: model,
  },
  actions: {
    setCurrentPath({
      commit
    }, path) {
      return commit('setCurrentPath', path)
    }
  },
  mutations: {
    setCurrentModel(state, currentModel) {
      console.log("currentModel", currentModel)
      state.currentModel = currentModel;
    },
    setCurrentModelAdd(state, currentModelAdd) {
      console.log("currentModelAdd", currentModelAdd)
      state.currentModelAdd = currentModelAdd;
    },
  }
})