<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="treeOptions" @node-click="handleNodeClick"></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home')">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in dslArray"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="currentModelAdd"
            :dsl="panelDsl.rows"
            :rules="rules"
            :tableInfo="panelDsl"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import { dslArray, asideTree, model, rules } from "./mock.js";
import Main from "./Main.js";
export default {
  name: "MainAdd",
  mixins: [Main],
  mounted() {
    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");
  },
  data() {
    return {
      rules,
      dslArray,
      asideTree,
      activeNames: ["项目参数"],
      /* 动态数据 */
      model /* 用于保存aside被点击时的数据 */
    };
  },
  methods: {
    goTo(command) {
      if (command === "home") {
        this.$confirm("是否放弃编辑?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push({ name: command });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    save() {
      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }
      /* 验证表单是否都符合rule */
      isOk(this.$refs.topTabe).then(isAllOk => {
        let doc = this.currentModelAdd;
        if (isAllOk) {
          const loading = this.$loading();
          console.log("this.currentModelAdd");
          /* save */
          this.$post("/proxy/save", {
            coll: "hqbj_dmgl_jbzd" /* collection */,
            doc /* document */
          }).then(response => {
            if (response.isSuccess) {
              /* 如果是修改，需要更新currentModel */
              if (this.$route.name === "update") {
                this.setCurrentModel(doc);
              }
              /* 此处缺少query()：query方法放在store里，作为全局方法，增加之后刷新asideTree */
              loading.close();
              this.$router.push({ name: "home" });
              this.$message({
                showClose: true,
                message: "保存成功",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "保存失败",
                type: "warning"
              });
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
    },
    handleNodeClick(data, node, vm) {
      let { lb: lbmc, lbbm, text: mc, bm } = data;
      this.setCurrentModelAdd({ lbmc, lbbm, mc, bm, ...data });
      console.log(node, vm);
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "text"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree.map(({ id, text, lb, lbbm }) => {
        return { id, text, lb, lbbm };
      });
      return treeOptions;
    }
  },
  components: {
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CPickupTree: () =>
      import(/* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue")
  }
};
</script>

