<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="treeOptions" @node-click="handleNodeClick"></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('add')">新增</el-button>
        <el-button @click="goTo('update')">修改</el-button>
        <el-button @click="goTo('remove')" :disabled="!(currentModel&&currentModel.id)">删除</el-button>
        <!-- {{currentModel}} -->
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in dslArray"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="currentModel"
            :dsl="panelDsl.rows"
            :rules="rules"
            :tableInfo="panelDsl"
            ref="topTabe"
            :disabled="true"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import { dslArray, asideTree, model, rules } from "./mock.js";
import Main from "./Main.js";
export default {
  name: "MainAdd",
  mixins: [Main],
  mounted() {
    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");
  },
  data() {
    return {
      rules,
      dslArray,
      asideTree,
      activeNames: ["项目参数"],
      /* 动态数据 */
      model /* 用于保存aside被点击时的数据 */
    };
  },
  methods: {
    goTo(command) {
      if (command === "add") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(this.currentModelAdd)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "update") {
        /* 初始化新增model */
        this.setCurrentModelAdd(
          (model => {
            return { ...model };
          })(this.currentModel)
        );
        return this.$router.push({
          name: command
        });
      }
      if (command === "remove") {
        this.$confirm(
          `确实要删除当前的表单?`,
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      }
    },
    deleteTableRow(row, index) {
      console.log("deleteTableRow", row, index);
    },
    console(...args) {
      console.log(args);
    },
    submit() {
      this.$refs.hytable[0].valid().then(isSuccess => {
        if (isSuccess) {
          this.$notify({
            title: "提示",
            message: "数据校验成功，可以提交表单"
          });
        }
      });
    },
    handleChange(activeNames) {
      console.log("activeNames", activeNames, activeNames === this.activeNames);
    },
    handleNodeClick(data, node, vm) {
      if (data.children) return (this.model = data);
      let { lb: lbmc, lbbm, text: mc, bm } = data;
      this.setCurrentModel({ lbmc, lbbm, mc, bm, ...data });
      console.log(node, vm);
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "text"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree;
      return treeOptions;
    }
  },
  components: {
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CPickupTree: () =>
      import(/* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue")
  }
};
</script>

